.inputGroup{background-color:#fff;width:100%;display:block;margin:0 0 0 20%;position:relative}.inputGroup label{padding:12px 30px;width:100%;display:block;text-align:left;color:#3C454C;cursor:pointer;position:relative;z-index:2;-webkit-transition:color 200ms ease-in;transition:color 200ms ease-in;overflow:hidden}.inputGroup label:before{width:1.5vw;height:1.5vw;border-radius:50%;content:'';background-color:#5562eb;position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%) scale3d(1, 1, 1);transform:translate(-50%, -50%) scale3d(1, 1, 1);-webkit-transition:all 300ms cubic-bezier(0.4, 0, 0.2, 1);transition:all 300ms cubic-bezier(0.4, 0, 0.2, 1);opacity:0;z-index:-1}.inputGroup label:after{width:1.5vw;height:1.5vw;content:'';border:2px solid #D1D7DC;background-color:#fff;background-repeat:no-repeat;background-position:2px 3px;border-radius:50%;z-index:2;position:absolute;right:30px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);cursor:pointer;-webkit-transition:all 200ms ease-in;transition:all 200ms ease-in}.inputGroup input:checked ~ label{color:#fff}.inputGroup input:checked ~ label:before{-webkit-transform:translate(-50%, -50%) scale3d(56, 56, 1);transform:translate(-50%, -50%) scale3d(56, 56, 1);opacity:1}.inputGroup input:checked ~ label:after{background-color:#54E0C7;border-color:#54E0C7}.inputGroup input{width:32px;height:32px;order:1;z-index:2;position:absolute;right:30px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);cursor:pointer;visibility:hidden}.form{padding:0 16px;max-width:550px;margin:50px auto;font-size:1.2vw;font-weight:600;line-height:36px}*,*::before,*::after{box-sizing:inherit}html{box-sizing:border-box}

